.tabs {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    border-bottom: 2px solid #4DA8DA;
}


.tabs > li {
    padding: 0.5em 1em;
    margin: 0 1em;
    color: black;
}

.current {
    background: #EEFBFB;
    color: black;
    font-weight: bold;
    border: 2px solid #4DA8DA;
    border-bottom: 0;   
    border-top-right-radius: 3px;
    border-top-left-radius: 3px;
    position: relative;
    top: 2px;
}